import { request } from "@umijs/max";
import { WithdrawalProgress } from "../commonType";
export const _getAlWithdrawal = async (): Promise<any[]> => {
  return request(" /api/v1/admin/private/withdrawal/all", {
    method: "get",
  });
};
export const _rejectWithdrawal = async (data: {
  reason: string;
  withdrawalId: string;
}): Promise<API.DefaultObjectResponse> => {
  return request("/api/v1/admin/private/withdrawal/{id}", {
    method: "put",
    params: { id: data.withdrawalId },
    data: { reason: data.reason, progress: WithdrawalProgress.REJECTED },
  });
};

export const _getTenantById = async (
  id: string
): Promise<API.DefaultObjectResponse> => {
  return request("/api/v1/admin/private/tenant/by-id", {
    method: "get",
    params: {
      id,
    },
  });
};
